<script>
import ImageSlider from "@/components/ClubLeader/ImageSlider.vue";
import axios from "axios";
import store from "@/store/store";
import defaultProfileImage from "@/assets/profile.png";

export default {
  name: "ClubProfile",
  components: {
    ImageSlider,
  },
  data() {
    return {
      images: [],
      data: {
        clubHashtag: [], // Ìï¥ÏãúÌÉúÍ∑∏ Í∏∞Î≥∏Í∞í Ï∂îÍ∞Ä
        tags: [], // ÌïÑÏöî ÏóÜÎäî Í≤ΩÏö∞ Ï†úÍ±∞ Í∞ÄÎä•
        leaderName: '', // ÎèôÏïÑÎ¶¨Ïû• Ïù¥Î¶Ñ Í∏∞Î≥∏Í∞í Ï∂îÍ∞Ä
        leaderHp: '', // Ï†ÑÌôîÎ≤àÌò∏ Í∏∞Î≥∏Í∞í Ï∂îÍ∞Ä
      },
      mainPhoto: defaultProfileImage,
      activeTab: "intro",
      showContactInfo: false,
    };
  },
  computed: {
    // ÎèôÏïÑÎ¶¨Ïû• Ïù¥Î¶Ñ ÌëúÏãú computed ÏÜçÏÑ± Ï∂îÍ∞Ä
    displayLeaderName() {
      return this.data.leaderName ? this.data.leaderName : "Ï†ïÎ≥¥ ÏóÜÏùå";
    },
    // Ï†ÑÌôîÎ≤àÌò∏ ÌëúÏãú computed ÏÜçÏÑ± ÏàòÏ†ï
    formattedPhoneNumber() {
      if (!this.data.leaderHp) {
        return "Ï†ïÎ≥¥ ÏóÜÏùå";
      }
      return this.data.leaderHp.replace(/(\d{3})(\d{4})(\d{4})/, "$1-$2-$3");
    },
    formattedCategory() {
      if (!this.data.clubCategories || this.data.clubCategories.length === 0) {
        return "Ïπ¥ÌÖåÍ≥†Î¶¨ ÏóÜÏùå";
      }
      return this.data.clubCategories.join(', ');  // ÏâºÌëú Îí§Ïóê Í≥µÎ∞± Ï∂îÍ∞Ä
    },
    instagramLink() {
      // Instagram ÎßÅÌÅ¨Í∞Ä httpÎ°ú ÏãúÏûëÌïòÏßÄ ÏïäÏúºÎ©¥ Ï∂îÍ∞Ä
      const instaUrl = this.data.clubInsta || '';
      if (instaUrl === '') return '';
      return instaUrl.startsWith('http') ? instaUrl : `https://${instaUrl}`;
    },
    formLink() {
      // Google Form ÎßÅÌÅ¨Í∞Ä httpÎ°ú ÏãúÏûëÌïòÏßÄ ÏïäÏúºÎ©¥ Ï∂îÍ∞Ä
      const formUrl = this.data.googleFormUrl || '';
      if (formUrl === '') return '';
      return formUrl.startsWith('http') ? formUrl : `https://${formUrl}`;
    },
    recruitmentIcon() {
      return this.data.recruitmentStatus === "OPEN"
          ? require("@/assets/green.svg") // Î™®Ïßë Ï§ë
          : require("@/assets/red.svg"); // Î™®Ïßë ÎßàÍ∞ê
    },
    recruitmentText() {
      return this.data.recruitmentStatus === "OPEN" ? "Î™®Ïßë Ï§ë" : "Î™®Ïßë ÎßàÍ∞ê";
    }

  },
  mounted() {
    this.pageLoadFunction();
    console.log("API ÏùëÎãµ Îç∞Ïù¥ÌÑ∞!!:", this.data);
    console.log("Ìï¥ÏãúÌÉúÍ∑∏ Îç∞Ïù¥ÌÑ∞:", this.data.clubHashtag);
    console.log("Ìï¥ÏãúÌÉúÍ∑∏ Îç∞Ïù¥ÌÑ∞:", this.data.clubRoomNumber);
    console.log("Ìï¥ÏãúÌÉúÍ∑∏ Îç∞Ïù¥ÌÑ∞:", this.data.clubRecruitment);
    console.log("Ìï¥ÏãúÌÉúÍ∑∏ Îç∞Ïù¥ÌÑ∞:", this.data.clubCategories);
  },
  methods: {
    // Ïù¥ÎØ∏ÏßÄ Ïä¨ÎùºÏù¥Îìú
    async pageLoadFunction() {
      const accessToken = store.state.accessToken;
      const clubUUID = store.state.clubUUID;

      try {
        const response = await axios.get(
            `${store.state.apiBaseUrl}/club-leader/${clubUUID}/summary`,
            {
              headers: {
                Authorization: `Bearer ${accessToken}`,
                "Content-Type": "application/json",
              },
            }
        );

        if (response.status === 200) {
          console.log("API ÏùëÎãµ Îç∞Ïù¥ÌÑ∞:", response.data);
          this.data = response?.data?.data || {};
        } else {
          console.error(`API Ïò§Î•ò: ${response.status} - ${response.statusText}`);
        }
        this.data = response?.data?.data || {};

        if (this.data.mainPhoto) {
          this.mainPhoto = this.data.mainPhoto;
        }

        const introPhotosPromises = this.data.introPhotos.map(async (url) => {
          try {
            const response = await axios.get(url, { responseType: "blob" });
            // Ï†ïÏÉÅÏ†ÅÏúºÎ°ú ÏùëÎãµ
            if (response.status === 200) {
              return URL.createObjectURL(response.data);
            }
          } catch (error) {
            console.error("Image fetch error:", error);
            return null;
          }
        });

        const introPhotosResults = await Promise.allSettled(
            introPhotosPromises
        );
        
        this.images = introPhotosResults
            .filter((result) => result.status === "fulfilled" && result.value)
            .map((result) => result.value);
      } catch (error) {
        console.error("Fetch error:", error);
      }
    },
    convertNewlinesToBr(text) {
      return text ? text.replace(/\n/g, "<br>") : "";
    },
    toggleContactInfo() {
      this.showContactInfo = !this.showContactInfo;
    },
    closeProfile() {
      window.close();
    }
  },
};
</script>

